import toUrlFriendlyText from "./to-url-friendly-text"

/**
 * Builds URLs.
 */
export default class UrlBuilder {
    /**
     * Builds a URL to an index of posts of the given category.
     * @param category Category.
     * @returns URL.
     */
    createCategoryUrl(category: string): string {
        const sanitized = this.removeBoundsSlashes(category)

        return `/${sanitized}`
    }

    /**
     * Builds a URL to an index of food reviews of the given food category.
     * @param foodCategory Food category.
     * @returns URL.
     */
    createFoodCategoryUrl(foodCategory: string): string {
        const sanitized = this.removeBoundsSlashes(foodCategory)

        return `/tipus-de-menjar/${sanitized}`
    }

    /**
     * Builds a URL to an index of food reviews of restaurants located
     * in the given neighbourhood.
     * @param neighbourhood Neighbourhood.
     * @returns URL.
     */
    createNeighbourhoodUrl(neighbourhood: string): string {
        const sanitized = this.removeBoundsSlashes(neighbourhood)

        return `/barri/${toUrlFriendlyText(sanitized)}`
    }

    private removeBoundsSlashes(chunk: string): string {
        let output = chunk

        if (output[0] === "/") {
            output = output.slice(1)
        }

        if (output[output.length - 1] === "/") {
            output = output.slice(0, -1)
        }

        return output
    }
}