import * as React from "react"
import { Link } from "gatsby"
import toEmoji from "../utils/to-emoji"

type Props = {
    filters: string[]
    title: string
    urlBuilder: (filter: string) => string
}

const FiltersList: React.FC<Props> = ({ title, filters, urlBuilder }) => {
    return (
        <div className="filters-list">
            <span className="filters-list-title">{title}:</span>
            <div className="filters">
                {
                    filters.map(c => <Link
                        to={urlBuilder(c)}
                        className="filter category">
                        {toEmoji(c)}
                    </Link>)
                }
            </div>
        </div>
    )
}

export default FiltersList
