import * as React from "react"
import { graphql, Link, PageProps } from "gatsby"

import Bio from "../components/bio"
import Seo from "../components/seo"
import Layout from "../components/layout"
import { Post } from "../models/post"
import extractCategoryFromSlug from "../utils/extract-category-from-slug"
import UrlBuilder from "../utils/url-builder"
import toEmoji from "../utils/to-emoji"
import FiltersList from "../components/filters-list"
import Collapse from "../components/collapse"

const takeAway = "per emportar"

const urlBuilder = new UrlBuilder

export type DataProps = {
  site: {
    siteMetadata: {
      title: string
    }
  }
  allMarkdownRemark: {
    nodes: Post[]
  }
}

type ContextProps = {
  categoryFolder?: string
  foodCategory?: string
  neighbourhood?: string
}

const PostsList: React.FC<PageProps<DataProps, ContextProps>> = ({ pageContext, data, location }) => {
  const {
    site: { siteMetadata },
    allMarkdownRemark: { nodes: posts }
  } = data

  const siteTitle = siteMetadata?.title

  if (!siteTitle) {
    throw new Error("A title is mandatory for every post")
  }

  const isFiltered = pageContext.categoryFolder
    || pageContext.foodCategory
    || pageContext.neighbourhood
  const onlyGastroPosts = posts.filter(p => p.fields.slug.includes("/gastro/"))
  const publishedPosts = process.env.GATSBY_DEBUG
    ? onlyGastroPosts
    : onlyGastroPosts.filter(post => !post.fields.slug.includes("/drafts/"))

  const filters = publishedPosts.reduce((filters, { frontmatter: { foodCategory, neighbourhood } }) => {

    if (foodCategory)
      filters.foodCategories.add(foodCategory)
    if (neighbourhood)
      filters.neighbourhoods.add(neighbourhood)

    return filters
  }, {
    foodCategories: new Set<string>,
    neighbourhoods: new Set<string>
  })

  const neighbourhoods = Array.from(filters.neighbourhoods.values())
    .sort()

  return (
    <Layout location={location} title={siteTitle}>
      {
        isFiltered
          ? <div id="filter-alert">
            <p>
              ‚òùÔ∏è Estas filtrant per
              {" "}
              <strong className="current-filter">
                  {
                    pageContext.categoryFolder
                    && pageContext.categoryFolder.slice(1, -1)
                    || pageContext.foodCategory
                    || pageContext.neighbourhood
                  }
                <Link
                  to="/"
                  className="clear-filter-btn"
                >
                  ‚ùå
                </Link>
              </strong>
            </p>
          </div>
          : <>
            <Bio />
            <Collapse titles={["‚ûï Mostra els filtres", "‚ûñ Amaga els filtres"]}>
              <FiltersList
                title="Tipus de menjar"
                filters={Array.from(filters.foodCategories.values())}
                urlBuilder={f => urlBuilder.createFoodCategoryUrl(f)} />
              <FiltersList
                title="Ubicaci√≥"
                filters={neighbourhoods}
                urlBuilder={f => urlBuilder.createNeighbourhoodUrl(f)} />
              <br />
            </Collapse>
          </>
      }
      <ol style={{ listStyle: `none` }}>
        {publishedPosts.map(({ frontmatter, fields, excerpt }) => {
          const { slug } = fields
          const {
            title = slug,
            foodCategory,
            neighbourhood,
            date,
            description
          } = frontmatter
          const category = extractCategoryFromSlug(slug)

          return (
            <li key={slug}>
              <article
                className="post-list-item"
                itemScope
                itemType="http://schema.org/Article"
              >
                <header>
                  {
                    isFiltered
                      ? null
                      : <Link
                        to={urlBuilder.createCategoryUrl(category)}
                        className="filter category">
                        {toEmoji(category)}
                      </Link>
                  }
                  {
                    pageContext.foodCategory || !foodCategory
                      ? null
                      : <Link
                        to={urlBuilder.createFoodCategoryUrl(foodCategory)}
                        className="filter food-category"
                      >
                        {toEmoji(foodCategory)}
                      </Link>
                  }
                  {
                    pageContext.neighbourhood || !neighbourhood
                      ? null
                      : <Link
                        to={urlBuilder.createNeighbourhoodUrl(neighbourhood)}
                        className={`filter ${neighbourhood === takeAway ? "take-away" : "neighbourhood"}`}>
                        {neighbourhood === takeAway ? "üö¥" : neighbourhood}
                      </Link>
                  }
                  <h2>
                    <Link to={slug} itemProp="url">
                      <span itemProp="headline">{title}</span>
                    </Link>
                  </h2>
                  <small>{date}</small>
                </header>
                <section>
                  <p
                    dangerouslySetInnerHTML={{
                      __html: description || excerpt,
                    }}
                    itemProp="description"
                  />
                </section>
              </article>
            </li>
          )
        })}
      </ol>
      {
        isFiltered
          ? <footer className="filtered-list-footer">
            <Bio />
          </footer>
          : <></>
      }
    </Layout>
  )
}

export default PostsList

export const Head = () => <Seo title="Tots els articles" />

export const pageQuery = graphql`
  query PostsList(
    $categoryFolder: String
    $foodCategory: String
    $neighbourhood: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: { 
        fields: { slug: { regex: $categoryFolder } },
        frontmatter: { 
          foodCategory: { eq: $foodCategory } 
          neighbourhood: { eq: $neighbourhood } 
        }
      }, 
      sort: { frontmatter: { date: DESC } }
    ) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "DD MMMM YYYY", locale: "ca")
          title
          description
          foodCategory
          neighbourhood
        }
      }
    }
  }
`